<template>
  <div >
    <form v-if='!isReg'>
        <div>用户名:</div>
        <input type="text" v-model='name'>
        <div>密码:</div>
        <input type="text" v-model='password'>
        <button type="button" v-on:click='login()'>登录</button>
        <button type="button" v-on:click='reg()'>注册</button>
    </form>

    <form v-else>
        <div>用户名:</div>
        <input type="text" v-model='name'>
        <div>密码:</div>
        <input type="text" v-model='password'>
        <div>再次输入密码:</div>
        <input type="text" v-model='repeat'>
        <button type="button" v-on:click='addUser()'>确认</button>
        <button type="button" v-on:click='cancel()'>取消</button>
    </form>
  </div>
</template>

<script>
// @ is an alias to /src


export default {
  name: 'LogIn',
  data:function(){
    return{
      isReg: false,
      name:'',
      password:'',
      repeat:''
    }
  },
  methods: {
    login(){
      if(localStorage.getItem('name')===this.name && localStorage.getItem("password") === this.password){
        this.name='',
        this.password='',
        this.$router.push('/Home/List')
      }else{    
        alert('用户名密码不正确！！')
      }
    },
    reg () {
        this.isReg = true
      },
    cancel(){
      this.isReg=false
    },
    addUser(){
       if (this.password === this.repeat){
          localStorage.setItem("name", this.name);
          localStorage.setItem("password", this.password);
          this.name = '';
          this.password = '';
          this.repeat = '';
          this.isReg = false;
        }else{
          alert('两次密码输入不一致')
        }
    }
  }
}
</script>
<style>
input {
  margin:10px;
}
button {
  margin:10px;
  width: 60px;
  height: 40px;
}
</style>

